.btn {
  display: flex;
  width: 100%;
  height: 56px;
  margin-top: 40px;
  padding: 0;
  border-radius: 40px;
  font-size: 16px;
  font-weight: 700;
  cursor: pointer;
  border: none;
  background-color: #fff;
  color: #1d1d1d;

  @include on-desktop {
    width: 400px;
  }

  &:hover {
    box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.40);
  }

  &:active {
    box-shadow: 1px 4px 16px 0 rgba(232, 232, 240, 0.64);
  }

  &__link {
    box-sizing: border-box;
    display: block;
    width: 100%;
    height: 100%;
    padding: 18px 48px;
    text-decoration: none;
    font-weight: 700;
    color: #1d1d1d;
  }

  &--centered {
    display: flex;
    justify-content: center;
  }

  &--form {
    margin-top: 16px;
  }
}
